/* ToolTips */
.tooltips:hover:after{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  background: #444;
  border-radius: 8px;
  color: #fff;
  content: attr(title);
  margin: 2px auto 0;
  font-size: 12px;
  padding: 2px;
  width: auto;
}
.tooltips:hover:before{
  content: "";
}

/* Word Wrapper */
.wordWrap {
